<template>
  <section class="code-view view-background">
    <header-view :title="title" :fixed="fixed" :routerName="routerName"></header-view>
    <div class="code-contanier">
      <div class="code-bg">
        <div class="code-content">
          <div class="code-img">
            <div class="mycode">
              <div class="mycode-img"><img :src="img"></div>
            </div>
            <div class="code-help">长按保存到本地</div>
          </div>
          <div class="code-bottom">扫码加入我们吧</div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '我的二维码',
        fixed: false,
        routerName: 'myRecommend',
        img: '/static/image/code.png'
      }
    },
    created: function () {
       this.img = localStorage.qrCode
    }
    
  }
</script>

<style>
/* 二维码页面 */

.code-view {
  height: 100%;
}

.code-contanier {
  width: 100%;
  height: 4.51rem;
  padding: .05rem .1rem;
}

.code-bg {
  padding: .21rem .15rem;
  width: 100%;
  height: 100%;
  background: url(/static/image/recommend/QRcode_card_bg@3x.png);
  background-size: 100% 100%;
}

.code-content {
  height: 100%;
  width: 100%;
}

.code-img {
  width: 100%;
  height: calc(100% - .62rem);
  position: relative;
}

.code-bottom {
  width: 100%;
  text-align: center;
  vertical-align: middle;
  height: .62rem;
  line-height: .62rem;
  color: #333;
}

.code-help {
  height: .44rem;
  line-height: .44rem;
  text-align: center;
}

.mycode {
  position: relative;
  height: calc(100% - .44rem);
}

.mycode-img {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 2.5rem;
  width: 2.5rem;
}
</style>


